<template lang="">
  <v-app-bar app color="secondary">
    <div class="d-flex align-center">
      <v-img
        alt="TTH Logo"
        class="shrink mr-2"
        contain
        src="../assets/logo.png"
        transition="scale-transition"
        width="40"
      />

      <v-card-title primary-title>
        TTH - Deck Manager
      </v-card-title>
    </div>

    <v-spacer></v-spacer>
    <input
          id="fileUpload"
          type="file"
          ref="file"
          hidden
          accept=".txt"
          @change="importDeck"
        />
    <v-btn target="_blank" text @click="$refs.file.click()">
      <span >Import</span>
      <v-icon>mdi-arrow-up-bold-box-outline</v-icon>
    </v-btn>
    <v-btn target="_blank" text @click="printDeck()">
      <span class="mr-2">Export</span>
      <v-icon>mdi-arrow-down-bold-box-outline</v-icon>
    </v-btn>
  </v-app-bar>
</template>
<script>
export default {
  methods: {
    printDeck() {
      this.$emit("print-deck");
    },
    importDeck(ev){
      this.$emit("import-deck",ev);
    }
  }
};
</script>
<style lang=""></style>
