<template lang="">
  <v-container style="overflow:hidden; width:100%;">
    <v-card style="left:10px">
      <v-card-title primary-title>
        {{ deckName }}
        <v-spacer></v-spacer>
        <v-btn
          class="mx-2"
          fab
          dark
          small
          color="secondary"
          v-on:click="isHidden = !isHidden"
        >
          <v-icon dark>
            mdi-pencil
          </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text v-show="!isHidden">
        <v-text-field filled label="New Deckname" v-model="newName"></v-text-field>
        <v-btn color="secondary" v-on:click="saveDeckName()">Save</v-btn>
      </v-card-text>
    </v-card>
    <DeckView
      :deckList="deckList"
      :sideList="sideList"
      :deckCount="deckCount"
      :sideCount="sideCount"
      v-on:add-card="addCard"
      v-on:remove-card="removeCard"
      v-on:add-side="addSide"
      v-on:remove-side="removeSide"
    />
  </v-container>
</template>
<script>
import DeckView from "../components/DeckView.vue";

export default {
  name: "RightPage",
  components: { DeckView },
  methods: {
    removeCard(card) {
      this.$emit("remove-card", card);
    },
    addCard(card) {
      this.$emit("add-card", card);
    },
    removeSide(card) {
      this.$emit("remove-side", card);
    },
    addSide(card) {
      this.$emit("add-side", card);
    },
    saveDeckName() {
      this.isHidden = true;
      this.$emit("change-deckname", this.newName);
    }
  },
  data() {
    return { isHidden: true,
    newName: "" };
  },
  props: [
    "deckList",
    "deckCount",
    "sideList",
    "sideCount",
    "deckName",
    "editName"
  ]
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
  font-size: 2em;
}
</style>
