<template>
  <v-container>
    <v-form v-on:submit.prevent="handleSearch">
      <v-text-field
        v-model="nameField"
        :hint="'Try searching for \'Soulfire Grand Master\'.'"
        :label="'Search for a specific card'"
        v-on:click:append="$emit('name-search', this.target.nameField)"
        @submit="handleSearch"
      />
    </v-form>
    <v-btn color="primary" @click="handleSearch"> Search </v-btn>
  </v-container>
</template>

<script>
export default {
  name: "NameSearch",
  data() {
    return {
      nameField: ""
    };
  },
  methods: {
    handleSearch() {
      this.$emit("name-search", this.nameField);
    }
  }
};
</script>
