<template lang="">
  <v-container
    v-if="results.length > 0"
    style="overflow:hidden; width:100%; right:10px"
  >
    <h2>{{ results.length }} Card(s) Found</h2>
    <v-list
      maxHeight="61vh"
      style="overflow-y: auto; width:107%; overflow-x:hidden"
    >
      <v-card
        outlined
        class="mx-auto"
        v-for="card in results"
        width="98%"
        max-width="640px"
        :key="card.mtgo_id"
      >
        <v-row>
          <v-col sm="4" md="4" class="hidden-xs-only">
            <v-img
              v-if="card.image_uris"
              :src="card.image_uris.normal"
              height="250px"
              width="180px"
              style="margin:5px"
              align-center
              contain
            />
          </v-col>

          <v-col sm="8" md="8">
            <v-card-text>
              <v-list-item three-line>
                <v-list-item-content>
                  <v-list-item-title>
                    {{ card.name }}
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    {{ card.mana_cost }}
                  </v-list-item-subtitle>
                  <v-list-item-subtitle>
                    {{ card.type_line }}
                  </v-list-item-subtitle>

                  <v-list-item-subtitle
                    v-if="card.type_line.includes('Creature')"
                    >{{ card.power }}/{{ card.toughness }}</v-list-item-subtitle
                  >
                  <v-list-item-subtitle
                    v-if="card.type_line.includes('Planeswalker')"
                    >{{ card.loyalty }}</v-list-item-subtitle
                  >
                </v-list-item-content>
              </v-list-item>

              <v-card-actions class="pa-4">
                <v-btn @click="addCard(card)" color="secondary">
                  Add Card
                </v-btn>
                <v-btn @click="addSide(card)" color="secondary">
                  Add Side
                </v-btn>
              </v-card-actions>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    Oracle Text
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    {{ card.oracle_text }}
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-card-text>
          </v-col>
        </v-row>
      </v-card>
    </v-list>
  </v-container>
</template>
<script>
export default {
  props: ["results"],
  methods: {
    addCard(card) {
      this.$emit("add-card", card);
    },
    addSide(card) {
      this.$emit("add-side", card);
    }
  }
};
</script>
<style lang=""></style>
