<template lang="">
  <v-container>
    <v-form v-on:submit.prevent="handleSearch">
      <v-text-field
        v-model="nameField"
        :hint="'Try searching for soulfire'"
        :label="'Search for cards containing:'"
        v-on:click:append="$emit('gen-search', this.target.nameField)"
        @submit="handleSearch"
      />
    </v-form>
    <v-btn color="primary" @click="handleSearch"> Search </v-btn>
  </v-container>
</template>
<script>
export default {
  name: "GeneralSearch",
  data() {
    return {
      nameField: ""
    };
  },
  methods: {
    handleSearch() {
      this.$emit("gen-search", this.nameField);
    }
  }
};
</script>
<style lang=""></style>
