<template lang="">
  <v-container>
    <v-list
      maxHeight="75vh"
      style="padding:10px;overflow-y: auto; width:108%; overflow-x:hidden"
    >
      <v-card class="elevation-0 mx-auto" width="90%">
        <v-card-title class="justify-center">
          <h2>{{ deckCount }} Card(s)</h2>
        </v-card-title>

        <v-expansion-panels v-for="type in cardTypes" :key="type[0]">
          <v-card v-if="type[1] > 0" color="secondary" width="100%">
            <v-card-title class="justify-start">
              <h3>[ {{ type[1] }} ] ~ {{ type[0] }}</h3>
            </v-card-title>
          </v-card>

          <v-expansion-panel
            v-for="uniqueCard in type[2]"
            :key="uniqueCard[0].mtgo_id"
          >
            <v-expansion-panel-header max-width="640px">
              <v-row>
                <v-col xs="6" sm="4" md="6" lg="4">
                  <v-card class="elevation-0 text-left">
                    {{ uniqueCard[1] }}x
                    <v-btn
                      outlined
                      small
                      fab
                      color="secondary"
                      @click.native.stop="removeCard(uniqueCard[0])"
                    >
                      <v-icon>mdi-minus</v-icon>
                    </v-btn>
                    <v-btn
                      outlined
                      small
                      fab
                      color="secondary"
                      @click.native.stop="addCard(uniqueCard[0])"
                    >
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>
                    <v-btn
                      outlined
                      small
                      color="secondary"
                      @click.native.stop="moveToSide(uniqueCard[0])"
                    >
                    Side
                    </v-btn>
                  </v-card>
                </v-col>
                <v-col xs="6" sm="4" md="6" lg="4">
                  <v-card class="elevation-0 text-center">
                    <v-card-text>
                      {{ uniqueCard[0].name }}
                    </v-card-text>
                  </v-card>
                </v-col>

                <v-col class="hidden-xs-only hidden-md-only" cols="4">
                  <v-card class="elevation-0 text-right text-truncate">
                    <v-card-text>
                      {{ uniqueCard[0].mana_cost }}
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
            </v-expansion-panel-header>

            <v-expansion-panel-content>
              <v-row>
                <v-col>
                  <v-img
                    v-if="uniqueCard[0].image_uris"
                    :src="uniqueCard[0].image_uris.normal"
                    height="250px"
                    width="180px"
                    style="margin:5px"
                    align-center
                    contain
                  />
                </v-col>
                <v-col>
                  <v-row style="padding-top:10px">
                    <h4>{{ uniqueCard[0].name }}</h4>
                  </v-row>
                  <v-row style="padding-top:10px">
                    <h5>{{ uniqueCard[0].oracle_text }}</h5>
                  </v-row>
                  <v-row>
                    <v-list-item-subtitle
                      v-if="uniqueCard[0].type_line.includes('Creature')"
                      >{{ uniqueCard[0].power }}/{{
                        uniqueCard[0].toughness
                      }}</v-list-item-subtitle
                    >
                    <v-list-item-subtitle
                      v-if="uniqueCard[0].type_line.includes('Planeswalker')"
                      >{{ uniqueCard[0].loyalty }}</v-list-item-subtitle
                    >
                  </v-row>
                </v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-card>

      <v-card class="elevation-0 mx-auto" width="90%">
        <v-card-title class="justify-center">
          <h2>{{ sideCount }} Sideboard</h2>
          <v-container v-if="sideCount > 15">
            HEY MAN THATS TOO MANY SIDEBOARD CARDS</v-container
          >
        </v-card-title>

        <v-expansion-panels>
          <v-expansion-panel
            v-for="uniqueCard in sideList"
            :key="uniqueCard[0].mtgo_id"
          >
            <v-expansion-panel-header max-width="640px">
              <v-row>
                <v-col xs="6" sm="4" md="6" lg="4">
                  <v-card class="elevation-0 text-left">
                    {{ uniqueCard[1] }}x
                    <v-btn
                      outlined
                      small
                      fab
                      color="secondary"
                      @click.native.stop="removeSide(uniqueCard[0])"
                    >
                      <v-icon>mdi-minus</v-icon>
                    </v-btn>
                    <v-btn
                      outlined
                      small
                      fab
                      color="secondary"
                      @click.native.stop="addSide(uniqueCard[0])"
                    >
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>

                    <v-btn
                      outlined
                      small
                      color="secondary"
                      @click.native.stop="moveToMain(uniqueCard[0])"
                    >
                    Main
                    </v-btn>
                  </v-card>
                </v-col>
                <v-col xs="6" sm="4" md="6" lg="4">
                  <v-card class="elevation-0 text-center">
                    <v-card-text>
                      {{ uniqueCard[0].name }}
                    </v-card-text>
                  </v-card>
                </v-col>

                <v-col class="hidden-xs-only hidden-md-only" cols="4">
                  <v-card class="elevation-0 text-right text-truncate">
                    <v-card-text>
                      {{ uniqueCard[0].mana_cost }}
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-row>
            </v-expansion-panel-header>

            <v-expansion-panel-content>
              <v-row>
                <v-col>
                  <v-img
                    v-if="uniqueCard[0].image_uris"
                    :src="uniqueCard[0].image_uris.normal"
                    height="250px"
                    width="180px"
                    style="margin:5px"
                    align-center
                    contain
                  />
                </v-col>
                <v-col>
                  <v-row style="padding-top:10px">
                    <h4>{{ uniqueCard[0].name }}</h4>
                  </v-row>
                  <v-row style="padding-top:10px">
                    <h5>{{ uniqueCard[0].oracle_text }}</h5>
                  </v-row>
                  <v-row>
                    <v-list-item-subtitle
                      v-if="uniqueCard[0].type_line.includes('Creature')"
                    >
                      {{ uniqueCard[0].power }}/{{ uniqueCard[0].toughness }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle
                      v-if="uniqueCard[0].type_line.includes('Planeswalker')"
                      >{{ uniqueCard[0].loyalty }}</v-list-item-subtitle
                    >
                  </v-row>
                </v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-card>
    </v-list>
  </v-container>
</template>
<script>
export default {
  name: "DeckView",
  props: ["deckList", "deckCount", "sideList", "sideCount"],
  methods: {
    removeCard(card) {
      this.$emit("remove-card", card);
    },
    addCard(card) {
      this.$emit("add-card", card);
    },
    removeSide(card) {
      this.$emit("remove-side", card);
    },
    addSide(card) {
      this.$emit("add-side", card);
    },
    moveToMain(card) {
      this.$emit("remove-side", card);
      this.$emit("add-card", card);
    },
    moveToSide(card) {
      this.$emit("remove-card", card);
      this.$emit("add-side", card);
    }
  },
  computed: {
    cardTypes: function() {
      let list = [];
      let creatures = [];
      let artifacts = [];
      let enchantments = [];
      let planeswalkers = [];
      let instants = [];
      let sorceries = [];
      let lands = [];
      let creatureCount = 0;
      let artifactCount = 0;
      let enchantmentCount = 0;
      let planeswalkerCount = 0;
      let instantCount = 0;
      let sorceryCount = 0;
      let landCount = 0;
      this.deckList.forEach(cardLine => {
        let cardType = cardLine[0].type_line;
        let cardCount = cardLine[1];

        switch (true) {
          case cardType.includes("Creature"):
            {
              creatures.push(cardLine);
              creatureCount = creatureCount + cardCount;
            }
            break;
          case cardType.includes("Artifact"):
            {
              artifacts.push(cardLine);
              artifactCount = artifactCount + cardCount;
            }
            break;
          case cardType.includes("Enchantment"):
            {
              enchantments.push(cardLine);
              enchantmentCount = enchantmentCount + cardCount;
            }
            break;
          case cardType.includes("Planeswalker"):
            {
              planeswalkers.push(cardLine);
              planeswalkerCount = planeswalkerCount + cardCount;
            }
            break;
          case cardType.includes("Instant"):
            {
              instants.push(cardLine);
              instantCount = instantCount + cardCount;
            }
            break;
          case cardType.includes("Sorcery"):
            {
              sorceries.push(cardLine);
              sorceryCount = sorceryCount + cardCount;
            }
            break;
          case cardType.includes("Land"):
            {
              lands.push(cardLine);
              landCount = landCount + cardCount;
            }
            break;
        }
      });

      list.push(["Creatures", creatureCount, creatures]);
      list.push(["Artifacts", artifactCount, artifacts]);
      list.push(["Enchantments", enchantmentCount, enchantments]);
      list.push(["Planeswalkers", planeswalkerCount, planeswalkers]);
      list.push(["Instants", instantCount, instants]);
      list.push(["Sorceries", sorceryCount, sorceries]);
      list.push(["Lands", landCount, lands]);
      return list;
    }
  }
};
</script>
<style lang=""></style>
