<template>
  <v-container
    v-if="results.length == 1"
    style="overflow:hidden; width:100%; right:10px"
  >
    <v-list maxHeight="66vh" style="overflow-y: hidden; overflow-x:hidden">
        <v-container>
          <v-row>
            <v-col xs="12" sm="4" md="12" lg="4" >
              <v-img
                v-if="results[0].image_uris"
                :src="results[0].image_uris.large"
                height="250px"
                style="margin:5px"
                align-center
                contain
              />
            </v-col>

            <v-col>
              <v-card-text>
                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title>
                      {{ results[0].name }}
                    </v-list-item-title>
                    <v-list-item-subtitle>
                      {{ results[0].mana_cost }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle>
                      {{ results[0].type_line }}
                    </v-list-item-subtitle>
                    <v-spacer></v-spacer>

                    <v-list-item-subtitle class="text-wrap">
                      {{ results[0].oracle_text }}
                    </v-list-item-subtitle>
                    <v-spacer></v-spacer>

                    <v-list-item-subtitle
                      v-if="results[0].type_line.includes('Creature')"
                      >{{ results[0].power }}/{{
                        results[0].toughness
                      }}</v-list-item-subtitle
                    >
                    <v-list-item-subtitle
                      v-if="results[0].type_line.includes('Planeswalker')"
                      >{{ results[0].loyalty }}</v-list-item-subtitle
                    >
                  </v-list-item-content>
                </v-list-item>
              </v-card-text>

              <v-card-actions class="pa-4">
                <v-btn color="secondary" v-on:click.stop="addCard(results[0])"
                  >Add Card</v-btn
                >
                <v-btn
                  color="secondary"
                  v-on:click.stop="addSide(results[0])"
                  >Add Side</v-btn
                >
              </v-card-actions>
            </v-col>
          </v-row>
        </v-container>
    </v-list>
  </v-container>
</template>

<script>
export default {
  name: "NameResult",
  props: ["results"],
  data() {
    return {};
  },
  methods: {
    addCard(card) {
      this.$emit("add-card", card);
    },
    addSide(card) {
      this.$emit("add-side",card)
    }
  }
};
</script>
