<template lang="">
  <div>
    <v-container style="overflow-y: hidden">
      <v-container>
        <v-btn small color="primary" @click="searchMode('exact')" class="mr-4">
          <v-icon dark>Exact</v-icon>
        </v-btn>

        <v-btn small color="primary" @click="searchMode('wide')" class="mr-4">
          <v-icon dark>Wide</v-icon>
        </v-btn>

        <v-btn small color="primary" @click="searchMode('third')" class="mr-4">
          <v-icon dark>...</v-icon>
        </v-btn>
      </v-container>

      <SpecificSearch
        v-show="searchType == 'exact'"
        v-on:name-search="nameSearch"
      />
      <SpecificResults
        v-show="searchType == 'exact'"
        :results="results"
        v-on:add-card="addCard"
        v-on:add-side="addSide"
      />

      <GeneralSearch
        v-show="searchType == 'wide'"
        v-on:gen-search="genSearch"
      />
      <GeneralResults
        v-show="searchType == 'wide'"
        :results="results"
        v-on:add-card="addCard"
        v-on:add-side="addSide"
      />

      <Error404 v-show="searchType == 'third'" />
    </v-container>
  </div>
</template>
<script>
import SpecificSearch from "../components/SpecificSearch.vue";
import SpecificResults from "../components/SpecificResults.vue";
import GeneralSearch from "../components/GeneralSearch.vue";
import GeneralResults from "../components/GeneralResults.vue";
import Error404 from "../components/Error404.vue";
export default {
  components: {
    SpecificSearch,
    SpecificResults,
    GeneralSearch,
    GeneralResults,
    Error404
  },
  data() {
    return {
      results: []
    };
  },
  methods: {
    genSearch(name) {
      const url = "https://api.scryfall.com/cards/search?q=";
      this.results = [];
      this.error = this.post = null;
      this.loading = true;
      let self = this;
      let searchURL = url + name;
      fetch(searchURL)
        .then(res => {
          if (res.status === 200) {
            return res.json();
          } else {
            self.error = "No results found";
          }
        })
        .then(response => {
          var cardsData = response;

          this.results = new Array(cardsData);
          this.results = this.results[0].data;
        })
        .catch(error => {
          console.error("Error:", error);
        });
    },
    nameSearch(name) {
      const url = "https://api.scryfall.com/cards/named?fuzzy=";
      this.results = [];
      this.error = this.post = null;
      this.loading = true;
      let self = this;
      let searchURL = url + name;
      fetch(searchURL)
        .then(res => {
          if (res.status === 200) {
            return res.json();
          } else {
            self.error = "No results found";
          }
        })
        .then(response => {
          var cardsData = response;

          this.results = new Array(cardsData);
        })
        .catch(error => {
          console.error("Error:", error);
        });
    },
    addCard(card) {
      this.$emit("add-card", card);
    },
    addSide(card) {
      this.$emit("add-side", card)
    },
    searchMode(type) {
      this.$emit("search-mode", type);
    }
  },
  props: ["searchType"]
};
</script>
<style lang=""></style>
