<template lang="">
  <v-container fluid class="overflow-hidden">
    <v-row no-gutters fill-height>
      <v-col xs="12" sm="12" md="6" lg="6" cols="12">
        <LeftPage
          v-on:add-card="addCard"
          v-on:add-side="addSide"
          v-on:search-mode="searchMode"
          :searchType="searchType"
        />
      </v-col>
      <v-col xs="12" sm="12" md="6" lg="6" cols="12">
        <RightPage
          :deckList="deckList"
          :deckCount="deckCount"
          :sideList="sideList"
          :sideCount="sideCount"
          :deckName="deckName"
          v-on:add-card="addCard"
          v-on:remove-card="removeCard"
          v-on:add-side="addSide"
          v-on:remove-side="removeSide"
          v-on:change-deckname="changeDeckName"
        />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import LeftPage from "./LeftPage.vue";
import RightPage from "./RightPage.vue";

export default {
  name: "Hero",

  data() {
    return {
      uniqueCards: [],
      deckList: [],
      sideList: [],
      deckName: "MyDecklist",
      searchType: "",
      deckCount: 0,
      sideCount: 0
    };
  },
  components: {
    LeftPage,
    RightPage
  },

  methods: {
    addCard(card) {
      this.deckCount++;
      let foundCard = false;
      this.deckList.forEach((uniqueCard, i) => {
        if (uniqueCard[0] === card) {
          let cardUpdate = new Array(card, uniqueCard[1] + 1);
          this.deckList.splice(i, 1, cardUpdate);
          foundCard = true;
        }
      });

      if (!foundCard) {
        let newCard = new Array(card, 1);
        this.deckList.push(newCard);
      }
    },
    removeCard(card) {
      this.deckCount--;
      this.deckList.forEach((uniqueCard, i) => {
        if (uniqueCard[0] === card) {
          let count = uniqueCard[1] - 1;

          if (count > 0) {
            let cardUpdate = new Array(card, count);
            this.deckList.splice(i, 1, cardUpdate);
          } else {
            this.deckList.splice(i, 1);
          }
        }
      });
    },
    addSide(card) {
      this.sideCount++;
      let foundCard = false;
      this.sideList.forEach((uniqueCard, i) => {
        if (uniqueCard[0] === card) {
          let cardUpdate = new Array(card, uniqueCard[1] + 1);
          this.sideList.splice(i, 1, cardUpdate);
          foundCard = true;
        }
      });

      if (!foundCard) {
        let newCard = new Array(card, 1);
        this.sideList.push(newCard);
      }
    },
    removeSide(card) {
      this.sideCount--;
      this.sideList.forEach((uniqueCard, i) => {
        if (uniqueCard[0] === card) {
          let count = uniqueCard[1] - 1;

          if (count > 0) {
            let cardUpdate = new Array(card, count);
            this.sideList.splice(i, 1, cardUpdate);
          } else {
            this.sideList.splice(i, 1);
          }
        }
      });
    },
    searchMode(type) {
      this.searchType = type;
    },
    printDeck() {
      let stringList = "";
      this.deckList.forEach(element => {
        stringList = stringList + element[1] + " " + element[0].name + "\n";
      });
      stringList = stringList + "\n";
      this.sideList.forEach(element => {
        stringList = stringList + element[1] + " " + element[0].name + "\n";
      });

      console.log(stringList);

      var element = document.createElement("a");
      element.setAttribute(
        "href",
        "data:text/plain;charset=utf-8," + encodeURIComponent(stringList)
      );

      var isValid = (function() {
        var rg1 = /^[^\\/:*?"<>|]+$/; // forbidden characters \ / : * ? " < > |
        var rg2 = /^\./; // cannot start with dot (.)
        var rg3 = /^(nul|prn|con|lpt[0-9]|com[0-9])(\.|$)/i; // forbidden file names
        return function isValid(fname) {
          return rg1.test(fname) && !rg2.test(fname) && !rg3.test(fname);
        };
      })();

      console.log(isValid(this.deckName));

      if (isValid(this.deckName)) {
        let deckName = this.deckName + ".txt";
        element.setAttribute("download", deckName);

        element.style.display = "none";
        document.body.appendChild(element);

        element.click();

        document.body.removeChild(element);
      }
    },
    importDeck(ev) {
      this.deckList = [];
      this.sideList = [];
      this.deckCount = 0;
      this.sideCount = 0;
      const file = ev.target.files[0];
      const reader = new FileReader();

      reader.onload = e => this.readLines(e.target.result);
      reader.readAsText(file);
    },
    readLines(fileText) {
      var lines = fileText.split("\n");
      var sideCut = 0;
      //console.log(lines);
      for (let i = 0; i < lines.length; i++) {
        if (lines[i].length <= 1) {
          //console.log('blank')
          //console.log(i)
          sideCut = i;
          i = lines.length;
        }
      }

      let sideboard = lines.splice(sideCut);
      sideboard = sideboard.splice(1);
      sideboard.pop();
      //console.log(lines)
      //console.log(sideboard)

      this.importMainboard(lines);
      this.importSideboard(sideboard);
    },
    importMainboard(deck) {
      deck.forEach(line => {
        let quantity = parseInt(line.substring(0, 1));
        let cardName = line.substring(2, line.length);

        this.error = this.post = null;
        this.loading = true;
        let self = this;
        let searchURL =
          "https://api.scryfall.com/cards/named?fuzzy=" + cardName;
        fetch(searchURL)
          .then(res => {
            if (res.status === 200) {
              return res.json();
            } else {
              self.error = "No results found";
            }
          })
          .then(response => {
            var cardsData = response;
            var cardReturn = new Array(cardsData);
            //console.log(cardReturn[0].name)
            for (let i = 0; i < quantity; i++) {
              this.addCard(cardReturn[0]);
            }
          })
          .catch(error => {
            console.error("Error:", error);
          });
      });
    },
    importSideboard(deck) {
      deck.forEach(line => {
        let quantity = parseInt(line.substring(0, 1));
        let cardName = line.substring(2, line.length);

        this.error = this.post = null;
        this.loading = true;
        let self = this;
        let searchURL =
          "https://api.scryfall.com/cards/named?fuzzy=" + cardName;
        fetch(searchURL)
          .then(res => {
            if (res.status === 200) {
              return res.json();
            } else {
              self.error = "No results found";
            }
          })
          .then(response => {
            var cardsData = response;
            var cardReturn = new Array(cardsData);
            console.log(cardReturn[0].name);
            for (let i = 0; i < quantity; i++) {
              this.addSide(cardReturn[0]);
            }
          })
          .catch(error => {
            console.error("Error:", error);
          });
      });
    },
    changeDeckName(name) {
      this.deckName = name;
    }
  }
};
</script>
<style lang=""></style>
